.urgentLink {
  display: inline-flex;        
  align-items: center;           
  justify-content: center;

  padding: 6px 14px;            
  font-weight: 500;
  text-decoration: none;
  border-radius: 6px;

  color: #418C6B;
  border: 1px solid #418C6B;
  background-color: #f0f4f8;

  cursor: pointer;

  transition: background-color 0.2s ease,
              color 0.2s ease,
              transform 0.2s ease;
}

.urgentLink:hover {
  background-color: #e0ebf0;
  color: #418C6B;
  transform: translateY(-1px);
}
